$(document).ready(function() {
    const $container = $('.gerne-ranking > .slider-container-rank > .slider-wrapper');
    const $btnLeft = $('.gerne-ranking > .slider-container-rank > .nav-button.left');
    const $btnRight = $('.gerne-ranking > .slider-container-rank > .nav-button.right');
    let currentIndex = 0;
    const itemsToShow = 4;
    const gap = parseInt($('.performance-list-genre').css('gap')) || 0;

    const updateSliderPosition = () => {
        const $items = $container.find('.performance-info'); // Ïä¨ÎùºÏù¥Îçî ÏïÑÏù¥ÌÖú Ïû¨Ï¥àÍ∏∞Ìôî
        const itemWidth = $items.first().outerWidth() + gap;
        $container.css('transform', `translateX(-${currentIndex * itemWidth}px)`);
        updateButtonStates();
    };

    const updateButtonStates = () => {
        const $items = $container.find('.performance-info'); // Ïä¨ÎùºÏù¥Îçî ÏïÑÏù¥ÌÖú Ïû¨Ï¥àÍ∏∞Ìôî
        $btnLeft.prop('disabled', currentIndex <= 0);
        $btnRight.prop('disabled', currentIndex >= $items.length - itemsToShow);
    };

    $btnRight.off('click'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $btnRight.on('click', () => {
        console.log("test")
        const $items = $container.find('.performance-info'); // Ïä¨ÎùºÏù¥Îçî ÏïÑÏù¥ÌÖú Ïû¨Ï¥àÍ∏∞Ìôî
        if (currentIndex < $items.length - itemsToShow) {
            currentIndex++;
            updateSliderPosition();
        }
    });

    $btnLeft.off('click'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $btnLeft.on('click', () => {
        if (currentIndex > 0) {
            currentIndex--;
            updateSliderPosition();
        }
    });

    let isDragging = false;
    let dragStartX = 0;

    $container.off('mousedown'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $container.on('mousedown', (e) => {
        isDragging = true;
        dragStartX = e.pageX;
        e.preventDefault(); // Í∏∞Î≥∏ ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Í∏∞Îä• ÎπÑÌôúÏÑ±Ìôî
    });

    $(document).off('mouseup'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $(document).on('mouseup', (e) => {
        if (isDragging) {
            isDragging = false;
            const dragEndX = e.pageX;
            const dragDistance = dragEndX - dragStartX;
            const $items = $container.find('.performance-info'); // Ïä¨ÎùºÏù¥Îçî ÏïÑÏù¥ÌÖú Ïû¨Ï¥àÍ∏∞Ìôî
            const itemWidth = $items.first().outerWidth() + gap;

            if (Math.abs(dragDistance) > itemWidth / 2) {
                if (dragDistance < 0 && currentIndex < $items.length - itemsToShow) {
                    currentIndex++;
                } else if (dragDistance > 0 && currentIndex > 0) {
                    currentIndex--;
                }
            }
            updateSliderPosition(); // ÎßàÏö∞Ïä§ Î≤ÑÌäºÏùÑ ÎÜìÏïòÏùÑ Îïå Ïä¨ÎùºÏù¥Îìú ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
        }
    });

    updateSliderPosition();

    // ÏúàÎèÑÏö∞ Î¶¨ÏÇ¨Ïù¥Ï¶à Ïãú Ïä¨ÎùºÏù¥Îçî Ïû¨Ï¥àÍ∏∞Ìôî
    $(window).off('resize'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $(window).on('resize', () => {
        updateSliderPosition();
    });
});
